@import url(https://fonts.googleapis.com/css?family=Roboto|Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


@use "sass:color";:root{--red: 0;--green: 0;--blue: 0}.quoteBlock{margin-left:-30px;display:flex;align-items:center;padding:8px 20px}.quoteBlock.input .box:hover,.quoteBlock.input .box :focus{background:#000;color:#fff}.quoteBlock .bonus_quote{flex-shrink:0;display:flex;align-items:center}.quoteBlock .bonus_quote .bonus{transform:scaleX(-1);font-size:30px}.quoteBlock .bonus_quote .bonus.hidden{visibility:hidden}.quoteBlock .bonus_quote .bonus svg{width:auto !important}.quoteBlock .bonus_quote .box{margin:0px;font-size:18px;text-transform:none;padding:10px;height:0%;min-width:100%;--red: 0;--green: 0;--blue: 0;border:rgb(0, 0, 0) 3px solid;border:rgb(var(--red), var(--green), var(--blue)) 3px solid}.quoteBlock .bonus_quote .box.correct{--threshold: 0.5;--border-threshold: 0.8;background:rgb(0, 0, 0) !important;background:rgb(var(--red), var(--green), var(--blue)) !important;--r: calc(var(--red) * 0.2126);--g: calc(var(--green) * 0.7152);--b: calc(var(--blue) * 0.0722);--sum: calc(var(--r) + var(--g) + var(--b));--perceived-lightness: calc(var(--sum) / 255);color:hsl(0, 0%, calc((var(--perceived-lightness) - var(--threshold)) * -10000000%)) !important;--border-alpha: calc((var(--perceived-lightness) - var(--border-threshold)) * 100) !important;border-width:.2em;border-style:solid;border-color:rgba(calc(0 - 50), calc(0 - 50), calc(0 - 50), var(--border-alpha)) !important;border-color:rgba(calc(var(--red) - 50), calc(var(--green) - 50), calc(var(--blue) - 50), var(--border-alpha)) !important}.quoteBlock .bonus_quote .box .quote{font-size:20px;text-align:center}.quoteBlock .bonus_quote .box .quote .player{font-style:italic}@media only screen and (max-width: 800px){.quoteBlock{display:flex;flex-direction:column;justify-content:center}.quoteBlock .votes{flex-direction:row !important;justify-content:center;align-items:center;width:100%;margin-left:30px !important}.quoteBlock .votes .vote{max-width:100px;margin:4px !important}}.quoteBlock .votes{margin:0px 5px;margin-left:35