;top:0;bottom:0;margin:auto}}

.scores{margin-top:20px}.scores .score{width:auto !important;min-width:150px;--threshold: 0.5;--border-threshold: 0.8;background:rgb(var(--red), var(--green), var(--blue)) !important;--r: calc(var(--red) * 0.2126);--g: calc(var(--green) * 0.7152);--b: calc(var(--blue) * 0.0722);--sum: calc(var(--r) + var(--g) + var(--b));--perceived-lightness: calc(var(--sum) / 255);color:hsl(0, 0%, calc((var(--perceived-lightness) - var(--threshold)) * -10000000%)) !important;--border-alpha: calc((var(--perceived-lightness) - var(--border-threshold)) * 100) !important;border-width:.2em;border-style:solid;border-color:rgba(calc(var(--red) - 50), calc(var(--green) - 50), calc(var(--blue) - 50), var(--border-alpha)) !important;font-weight:600;text-align:center;margin:0px 0px 8px 0px;padding:2px 5px;border-radius:5px;font-family:'Roboto', sans-serif;font-size:18px}.scores .score .judge{display:flex;align-items:center;justify-content:center;float:right;height:100%;font-size:20px;visibility:hidden;width:20px !important}.scores .score .judge svg{margin:auto}

.wordsmyth_input label{font-size:14px}.wordsmyth_input input{border:2px black solid;margin:5px 0px;padding:3px;font-size:18px;width:100%}.wordsmyth_input .error{color:red}

#writing{display:flex;flex-direction:column;justify-content:center;align-items:center}#writing #beginning{font-size:20px;padding:20px}#writing button{margin-top:20px}

.wordsmyth_timer{width:100%;height:14px;padding:3px;margin:3px 0px;background:rgba(0,0,0,0.4);border-radius:10px;overflow:hidden}.wordsmyth_timer .timer{border-radius:10px;transition:1s linear;height:100%;background:white}

#game{width:100%;max-width:600px;height:100%;margin:auto;display:flex;flex-direction:column;transition:.5s;align-items:center;justify-content:space-between}#game *{width:100%}#game #header{border-radius:5px;padding:3px 10px;margin-bottom:20px;--threshold: 0.5;--border-threshold: 0.8;background:rgb(var(--red), var(--green), var(--blue)) !important;--r: calc(var(--red) * 0.2126);--g: calc(var(--green) * 0.7152);--b: calc(var(--blue) * 0.0722);--sum: calc(var(--r) + var(--g) + var(--b));--perceived-lightness: calc(var(--sum) / 255);color:hsl(0, 0%, calc((var(--perceived-lightness) - var(--threshold)) * -10000000%)) !important;--border-alpha: calc((var(--perceived-lightness) - var(--border-threshold)) * 100) !important;border-width:.2em;border-style:solid;border-color:rgba(calc(var(--red) - 50), calc(var(--green) - 50), calc(var(--blue) - 50), var(--border-alpha)) !important}#game #header #top{display:flex}#game #header #top #title{font-size:200%;justify-content:center;align-self:center}#game #header #top #timer{font-size:200%;align-self:center;text-align:center}#game #header #top #info{width:auto !important;align-self:flex-end;text-align:right}#game #header #top #info #username{display:flex;flex-direction:row-reverse;font-size:100%}#game #header #top #info #username .judge{align-items:center;justify-content:center;width:20px !important;margin:2px 6px}#game #header #top #info #username .judge svg{margin:auto}#game #header #top #info #gameid{font-size:200%}#game #waiting{padding:20px 20px 0px;display:flex;flex-direction:column;justify-content:center;align-items:center;text-align:center;font-size:20px}#game #waiting button{margin-top:20px}#game #winner{display:flex;justify-content:center;align-items:center;text-align:center;font-size:20px}#game #winner .player{--threshold: 0.5;--border-threshold: 0.8;background:rgb(var(--red), var(--green), var(--blue)) !important;--r: calc(var(--red) * 0.2126);--g: calc(var(--green) * 0.7152);--b: calc(var(--blue) * 0.0722);--sum: calc(var(--r) + var(--g) + var(--b));--perceived-lightness: calc(var(--sum) / 255);color:hsl(0, 0%, calc((var(--perceived-lightness) - var(--threshold)) * -10000000%)) !important;--border-alpha: calc((var(--perceived-lightness) - var(--border-threshold)) * 100) !important;border-width:.2em;border-style:solid;border-color:rgba(calc(var(--red) - 50), calc(var(--green) - 50), calc(var(--blue) - 50), var(--border-alpha)) !important;margin-right:5px;max-width:100px;font-weight:600;padding:4px 5px;border-radius:10px;font-family:'Roboto', sans-serif;text-align:center}#game #quoteorigin{font-size:25px;align-self:flex-start;margin-bottom:20px}#game #emptyquotes{font-size:25px;align-self:flex-start;text-align:center;margin:20px 0px}

#join{max-width:600px;height:100%;margin:auto;display:flex;flex-direction:column;justify-content:center}#join .nav{display:flex;justify-content:center;flex-wrap:1;margin:20px;margin-top:0px}#join .nav .navitem{padding:20px;font-size:25px;cursor:pointer}#join .nav .navitem.selected{background:#000;color:#fff}#join .nav .navitem:hover{text-decoration:underline}#join #form{display:flex;flex-direction:column;justify-content:space-between}#join #form *{box-sizing:border-box}#join #form .wordsmyth_input{width:100%}#join #form input,#join #form button{width:100%}#join #form button{align-self:center;margin-top:15px}#join #form #params{display:flex}

.App{overflow:scroll;padding:25px;height:100vh;align-items:center;font-family:'Roboto Condensed', sans-serif;box-sizing:border-box}.App #title{font-size:40px}.App button{font-family:'Roboto Condensed', sans-serif;border:2px black solid;background:white;max-width:250px;margin:3px;padding:5px 10px;font-size:18px;cursor:pointer;text-transform:capitalize}.App button:hover{background:#000;color:#fff}.App button:disabled{cursor:default;background:white !important;color:black !important}.App .spacer{padding:2px}.App *{box-sizing:border-box}

